body {
  margin: 0;
  background-color: #008080;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "w95";
  src: url("./fonts/w-95-sans-serif.woff") format('woff');
}

@font-face {
  font-family: "w95";
  src: url("./fonts/w-95-sans-serif.woff") format('woff');
  font-weight: bold;
}

@font-face {
  font-family: "w95";
  src: url("./fonts/w-95-sans-serif.woff") format('woff');
  font-weight: bold;
  font-style: italic;
}

@font-face {
  font-family: "w95";
  src: url("./fonts/w-95-sans-serif.woff") format('woff');
  font-style: italic;
}


.game-container {
  display: flex;
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.card{
  border-top: 2px solid white;
  border-left: 2px solid white;
  border-right: 2px solid #393939;
  border-bottom: 2px solid #393939;
  border-radius: 0;
  background: #C0C0C0;
}

.game-view {
  padding: 4px;
  height: 600px;
  width: 800px;
}
@media only screen and (max-width: 800px) {
  .game-view  {
    width: 350px;
    height: 600px;
  }
}

.game-header {
  line-height: 1.5;
  font-size: 11px;
  font-family: 'w95', woff;
  background: #000181;
  color: white;
  display: flex;
}

.game-header-item {
  width:33.33333%;
}

.info-header{
  padding: 4px;
  display: flex;
  justify-content: space-between;
}

.help{
  display: flex;
  align-items: flex-end;
}